// @flow
import React from 'react';
import { Row, Col, Card } from 'react-bootstrap';

import cardImg4 from '../../assets/images/logo-latam.png';
// components
import PageTitle from '../../components/PageTitle';
import MensajeBienvenida from './MensajeBienvenida';

const CardWithImage2 = () => {
    return (
        <Card className="d-block">
            <Card.Img src={cardImg4} />
            <Card.Body>
                <Card.Title as="h5"> LATAM Airlines Colombia</Card.Title>
                <Card.Text>
                    LATAM Airlines Colombia​​ es una aerolínea comercial de pasajeros colombiana y filial de LATAM
                    Airlines que cubre destinos nacionales e internacionales desde su sede central en el Aeropuerto
                    Internacional El Dorado de Bogotá y desde las principales ciudades de ese país.
                </Card.Text>
            </Card.Body>
            <Card.Body>
                <Card.Link href="https://es.wikipedia.org/wiki/LATAM_(Colombia)">Wikipedia</Card.Link>
            </Card.Body>
        </Card>
    );
};

const Latan = (props) => {
    return (
        <React.Fragment>
            <PageTitle breadCrumbItems={[{ label: 'Latan', path: '/ui/Latan', active: true }]} title={''} />
            <Row></Row>
            <Row>
                <Col lg={6}>
                    <MensajeBienvenida mensage={props.mensage} />
                </Col>
                <Col lg={6}>
                    <CardWithImage2 />
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default Latan;
