// @flow
import React from 'react';
import { Row, Col, Card, Carousel } from 'react-bootstrap';

const SlidesWithCaptions = () => {
    return (
        <Card>
            <Card.Body>
                <Carousel indicators={false}>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://source.unsplash.com/800x300/?sea"
                            alt="First slide"
                        />
                        <Carousel.Caption>
                            <h3>El fracaso</h3>
                            <p>El fracaso es la manera de empezar de nuevo, pero más inteligente</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://source.unsplash.com/800x300/?nature"
                            alt="Second slide"
                        />
                        <Carousel.Caption>
                            <h3>La felicidad </h3>
                            <p>La felicidad no es algo hecho. Proviene de tus propias acciones</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://source.unsplash.com/800x300/?island"
                            alt="Third slide"
                        />
                        <Carousel.Caption>
                            <h3>Oportunidad</h3>
                            <p>Cada día te brinda una nueva oportunidad</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </Card.Body>
        </Card>
    );
};

const Bienvenidos = (): React$Element<React$FragmentType> => {
    return (
        <React.Fragment>
            <Row>
                <Col lg={12}>
                    <SlidesWithCaptions />
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default Bienvenidos;
