// @flow
import React, { useState, useEffect } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
// components
import Avianca from './avianca';
import Copa from './copa';
import Latan from './Latan';
import PageNotFound from './PageNotFound';
import MensajeBienvenida from './MensajeBienvenida';
import Bienvenidos from './Bienvenidos';

function formatext(str) {
    let text = str.replace('/pages/aerolineas/', '').replace('/', '').replace('Pages/Aerolineas/', 'Aerolineas');
    return text.charAt(0).toUpperCase() + text.slice(1);
}
const Aerolineas = () => {
    const INIT_STATE = {
        selected: 'Aerolineas',
    };
    const appLocation = useLocation();
    const appurl = appLocation.pathname;
    const [state, setState] = useState(INIT_STATE);
    useEffect(() => {
        if (appurl || appurl === null) {
            setState({ selected: appurl });
        }
    }, [appurl]);
    useEffect(() => {
        if (document.body) document.body.classList.remove('authentication-bg');
    }, []);
    const opcion = formatext(state.selected);

    return (
        <>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            {(() => {
                                switch (opcion) {
                                    case 'Pages/Aerolineas':
                                        return (
                                            <Row>
                                                <Col lg={12}>
                                                    <MensajeBienvenida mensage={opcion} />
                                                    <Bienvenidos />
                                                </Col>
                                            </Row>
                                        );
                                    case 'Latan':
                                        return (
                                            <Row>
                                                <Col lg={12}>
                                                    {' '}
                                                    <Latan mensage={opcion} />
                                                </Col>
                                            </Row>
                                        );
                                    case 'Avianca':
                                        return (
                                            <Row>
                                                <Col lg={12}>
                                                    <Avianca mensage={opcion} />
                                                </Col>
                                            </Row>
                                        );
                                    case 'Copa':
                                        return (
                                            <Row>
                                                <Col lg={12}>
                                                    {' '}
                                                    <Copa mensage={opcion} />
                                                </Col>
                                            </Row>
                                        );

                                    default:
                                        return (
                                            <Row>
                                                <Col sm={12}>
                                                    {' '}
                                                    <PageNotFound />
                                                </Col>
                                            </Row>
                                        );
                                }
                            })()}
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </>
    );
};
export default withRouter(Aerolineas);
